/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

 .wp-block-wtrb-boxslogans {
	&.wtrbs__mcols_1 {
		grid-template-columns: repeat(1, 1fr);
	}
	&.wtrbs__tcols_2 {
		grid-template-columns: repeat(2, 1fr);
	}
	&.wtrbs__dcols_3 {
		grid-template-columns: repeat(3, 1fr);
	}
	&.wtrbs__dcols_4 {
		grid-template-columns: repeat(4, 1fr);
	}
}

.wtrbmr__label_flex,
.wtrbmr__responsive_items {
	margin-bottom: 16px;
}
.wtrbmr__label_flex {
	align-items: center;
	display: flex;
}
p.wtrbmr__label_nomargin {
	flex: 1;
	margin: 0;
}
.wtrbmr__devices {
	align-items: center;
	display: flex;
}
button.wtrbmr__device {
	background: none;
	border: none;
	cursor: pointer;
	margin: 0;
	padding: 0;
	&.middle_device {
		margin: 0 2px;
	}
	svg {
		fill: #d4d5ff;
		height: 10px;
		width: 15px;
	}
	&.active {
		svg {
			fill: #4f4f7c;
		}
	}
}
.wtrbmr__items {
	.dashicon {
		&.dashicons {
			color: #4f4f7c;
			font-size: 16px;
			line-height: 20px;
		}
	}
}
.block-list-appender {
	position: relative !important;
}

.wtrb-tabs-panel {
	.components-tab-panel__tabs {
		justify-content: space-around;
		align-items: center;
	}
	.wtrb__tab {
		width: 100%;
		justify-content: center;
		border-top: 1px solid #e0e0e0;

		&.active_tab {
			border-top: 2px solid #4f4f7c;
			color: #4f4f7c;
			background: #d4d5ff;
		}
		&.wtrb__middle {
			border-left: 1px solid #e0e0e0;
			border-right: 1px solid #e0e0e0;
		}
	}
	.wtrb__titles_group {
		margin-bottom: 15px;
		display: block;
	}
	.components-button-group {
		box-sizing: border-box !important;
		display: flex;
		flex-wrap: nowrap;

		.components-button {
			box-shadow: inset 0 0 0 1px #d4d4ff !important;
			display: block !important;
			padding: 0 !important;
			width: 100%;
			&.is-pressed {
				background: #4f4f7c !important;
			}
		}
	}
	.components-select-control {
			flex-direction: row !important;
			align-items: center !important;

		.components-flex-item {
			flex: 1 !important;

			label {
				margin: 0 !important;
			}
		}
	}
}

p.wtrb__label {
	line-height: 1.5;
	margin-bottom: 1em !important;
}
.wtrb__color_picker {
	align-items: center;
	display: flex;
	margin: 16px 0;
	position: relative;
}
p.wtrb__color_picker_label {
	flex-grow: 1;
	margin: 0;
}
button.wtrb__color_indicator {
	background: none;
	border: none;
	cursor: pointer;
	height: 25px;
	position: relative;
	width: 25px;

	span.component-color-indicator {
		background-image: linear-gradient(45deg,#ddd 25%,transparent 0),linear-gradient(-45deg,#ddd 25%,transparent 0),linear-gradient(45deg,transparent 75%,#ddd 0),linear-gradient(-45deg,transparent 75%,#ddd 0);
		background-position: 0 0,0 5px,5px -5px,-5px 0;
		background-size: 10px 10px;
		height: 100%;
		left: 0;
		position: absolute;
		top: 0;
		width: 100%;
	}
}
